import React from "react";
import {useState} from "react"


function Joke(props) {
    return (
        <div className={'joke'}>
            <span>{props.joke}</span>
        </div>
    );
}


function Jokes() {
    const [jokes, setJokes] = useState([]);
    const jokesList = jokes.map((joke) => {
        return (<li><Joke joke={joke}/></li>)
    });

    const [date, setDate] = useState(new Date());

    const getJokes = async function () {
        const timestamp = date.getTime() / 1000 | 0;
        try {
            const response = await fetch(
                '/api/jokes/list?date=' + String(timestamp),
                {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                    }
                }
            );

            const responseData = await response.text();
            if (responseData.error) {
                alert(responseData.error);
                return;
            }

            setJokes(responseData);
        } catch (e) {
            alert(e.message)
        }
    }

    const newJokesBtn = <button onClick={getJokes}>Показать приколы</button>

    return (
        <div className="jokes-container">
            <div className="actions">
                <input type="date" onChange={(e) => setDate(new Date(e.target.value))}/>
                {newJokesBtn}
            </div>
            <ul>{jokesList}</ul>
        </div>
    );
}

export default Jokes;